<template>
  <div class="settings-index-page">
    <h1>关于</h1>

    <div class="about-info-box">
      <div><img class="wpm-icon" src="/image/icon_wpm.png" /></div>
      <h3>虫洞项目管理器</h3>

      <el-form label-width="100px">
        <!-- <el-form-item label="名称"> 虫洞项目管理器 </el-form-item> -->
        <el-form-item label="版本"> {{ about.version }} </el-form-item>
        <el-form-item label="版权"> {{ about.copyright }} </el-form-item>

        <el-form-item label="运行模式"> {{ about.profile }} </el-form-item>
        <el-form-item label="操作系统"> {{ about.os }} </el-form-item>
        <el-form-item label="处理器"> {{ about.arch }} </el-form-item>
        <el-form-item label="用户文件夹"> {{ about.home }} </el-form-item>
      </el-form>
    </div>
  </div>
</template>
    
<script>
export default {
  name: "settings-about",
  components: {},

  data() {
    return {
      about: {
        name: "",
        version: "",
      },
    };
  },

  methods: {
    fetch() {
      let p = {
        method: "GET",
        type: "about",
      };
      this.$store.dispatch("rest/execute", p).then((res) => {
        let vo = res.data;
        this.about = vo;
      });
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  
<style scoped>
.settings-index-page {
  text-align: left;
}

.about-info-box {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.wpm-icon {
  max-width: 96px;
  max-height: 96px;
}
</style>
